@media screen and (min-width: 375px) {
  .mk-container {
    margin-bottom: 80px;
  }
  .mk.swiper-wrapper {
    height: 302px;
    width: 497px;
  }
  .mk-photo-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    background-color: var(--dark-text-color);
    position: relative;
    overflow: hidden;
    z-index: -1;
  }
  .mk-photo-wrapper.chief {
    height: 280px;
    width: 80px;
  }
  .chief-img {
    display: block;
    width: 273.5px;
    height: 302.5px;
    position: absolute;
    border-radius: 15px;
    z-index: 1;
  }
  .mk-photo-wrapper.preview {
    height: 280px;
    width: 200px;
  }
  .preview-img {
    width: 157px;
    position: absolute;
  }
  .mk-photo-wrapper.zoom {
    height: 280px;
    width: 200px;
  }
  .dish-img {
    display: block;
    width: 500px;
    height: 500px;
    position: absolute;
    border-radius: 15px;
    z-index: 1;
  }
  .mk-card {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    margin-right: -141px;
    margin-left: 20px;
  }
  .mk-list {
    display: flex;
    flex-direction: row;
    gap: 8px;
  }
  .mk-item {
    display: flex;
  }
  .mk-info {
    display: flex;
    position: absolute;
    flex-direction: column;
    gap: 8px;
    bottom: 14px;
    max-height: 44px;
  }
  .mk-name {
    max-width: 156px;
    height: 28px;
    color: rgba(255, 255, 255, 0.6);
    text-transform: uppercase;
    text-align: center;
    font-size: 10px;
    font-weight: 500;
    line-height: 1.4;
  }
  .mk-region {
    color: rgba(255, 255, 255, 0.2);
    text-align: center;
    font-size: 8px;
    font-weight: 400;
    line-height: 1.5;
  }
}
@media screen and (min-width: 768px) {
  .mk-container {
    margin-bottom: 80px;
  }
  .mk.swiper-wrapper {
    height: 468px;
    width: 871px;
  }
  .mk-photo-wrapper.chief {
    height: 442px;
    width: 137px;
  }
  .chief-img {
    width: 420.7px;
    height: 465px;
  }
  .mk-photo-wrapper.preview {
    height: 442px;
    width: 351px;
  }
  .preview-img {
    width: 271px;
  }
  .mk-photo-wrapper.zoom {
    height: 442px;
    width: 351px;
  }
  .dish-img {
    width: 663px;
    height: 663px;
  }
  .mk-card {
    /* margin-right: -141px; */
    margin-left: 32px;
  }
  .mk-list {
    gap: 16px;
  }
  .mk-info {
    bottom: 32px;
    max-height: 44px;
  }
  .mk-name {
    max-width: 276px;
    height: 18px;
    font-size: 14px;
    line-height: 1.28;
  }
  .mk-region {
    font-size: 12px;
    line-height: 1.17;
  }
}
@media screen and (min-width: 1200px) {
  .mk-container {
    margin-bottom: 100px;
  }
}
